.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.ship {
		position: absolute;
		width: auto;
		height: 100%;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 0);
		img {
			width: auto;
			height: 100%;
			opacity: 0.5;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
