.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		padding: 5rem;
		margin: 0 auto;
		z-index: 2;
		@include max-md {
			padding: 3rem;
		}
		.elements-wrapper-absolute {
			border-radius: var(--border-radius);
			overflow: hidden;
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
