.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.frame {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 151%;
		transform: translate(-50%, -60%);
	}
	.ship {
		position: absolute;
		bottom: 0;
		left: 50%;
		height: 95%;
		width: auto;
		transform: translate(-50%, -0.75rem);
		img {
			width: auto;
			height: 100%;
			opacity: 0.3;
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.card {
				background-color: transparent;
				z-index: 5;
				text-align: center;
				overflow: hidden;
				border: none;
				.card-body {
					position: relative;
					padding: 0;
					h4 {
						margin: 0 0 1rem;
					}
					hr {
						display: block;
						width: 30%;
						height: 2px;
						margin: 0.5rem auto;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.event-name {
		position: relative;
		width: 80%;
		margin: 0 auto;
		.date {
			position: relative;
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			aspect-ratio: 1;
			background-color: var(--color-primary);
			color: var(--color-light);
			border-radius: 500px;
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				font-size: 32px;
				margin-bottom: 0;
				min-width: 30%;
			}
		}
		h4 {
			font-family: $font-secondary;
			padding-bottom: 10rem;
			@include max-lg {
				padding-bottom: 7rem;
			}
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
		padding: 2rem;
		margin-top: 1.8rem;
		margin-bottom: 2rem;
		@include max-md {
			margin-top: 1.3rem;
		}
		.elements-wrapper-absolute {
			overflow: hidden;
			border-radius: 0 0 var(--border-radius) var(--border-radius);
		}
		.btn-custom {
			position: absolute;
			bottom: -2rem;
			left: 50%;
			transform: translate(-50%, 50%);
			@include max-xl {
				min-width: 70%;
			}
			@include max-md {
				min-width: 80%;
			}
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
