.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			display: none;
		}
		@include min-lg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			display: block;
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
	}
	.couple-body {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.ship {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 112%;
			transform: translate(-50%, 8%);
		}
	}
	.couple-content {
		position: relative;
		padding: 5rem 0;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-bottom-right-radius: 20px;
		border-bottom-left-radius: 20px;
		z-index: 3;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		padding: 0.25rem 1.25rem 2rem;
		margin-top: 18rem;
		z-index: 5;
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 1;
			margin: -10rem auto 0;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: 500px;
				}
			}
			.frame {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 150%;
				transform: translate(-50%, -60%);
			}
		}
		.couple-description {
			position: relative;
			padding: 3rem 0 0;
			p {
				margin-bottom: 0;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: -2rem;
			left: 50%;
			transform: translate(-50%, 50%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
